<template>
  <div>
    <div
      class="bg-image"
      :style="`background-image: url(${pictureData.imagePath})`"
    >
      <div class="content" ref="content">
        <h1>{{ pictureData.title }}</h1>
        <h3>{{ pictureData.type }}</h3>
        <button @click="goTo" >More</button>
      </div>
    </div>
    <div class="blank"></div>
  </div>
</template>

<script>
export default {
  name: "Picture",
  props: {
    pictureData: Object,
    page: Number,
  },
  methods: {
    goTo() {
      this.$router.push('/detail');
      this.emitter.emit('pageCount');
      this.$refs.content.style.visibility = 'hidden';
    }
  }
}
</script>

<style>
div {
  box-sizing: border-box;
}
.blank {
  height: 0;
}
.bg-image {
  width: 100vw;
  height: 100vh;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.content {
  width: 500px;
  padding: 400px 50px;
  color: white;
  text-align: left;
  font-family: "classic B";
  text-shadow: darkslategrey 1px 1px 6px;
}
button {
  background-color: transparent;
  box-shadow:  darkslategrey 1px 1px 6px;
  width: 150px;
  padding: 10px;
  border: solid 2px white;
  border-radius: 10px;
  margin: 20px auto;
  color: white;
  text-shadow: darkslategrey 1px 1px 6px;
  font-family: "classic B";
  font-size: 20px;
  cursor: pointer
}
@media screen and (max-width: 768px) {
  .bg-image {
    height: 60vh;
  }
  .blank {
    height: 40vh;
  }
  .content {
    width: 100%;
    padding-top: 350px;
    text-align: center;
    color: slategray;
    text-shadow: none;
    opacity: 0.7;
    position: absolute;
    top: 200px;
  }
  .content h1 {
    font-size: 20px;
  }
  .content h2 {
    font-size: 12px;
  }
  .content button {
    background-color: transparent;
    box-shadow: none;
    width: 100px;
    padding: 5px;
    border: solid 2px slategray;
    border-radius: 10px;
    margin: 10px auto;
    color: slategray;
    font-family: "classic B";
    text-shadow: none;
    font-size: 16px;
  }
}
</style>