 <template>
  <div style="overflow: hidden">
    <div class="gallery-container" ref="container">
      <div class="gallery-item" v-for="(a, i) in pictureData" :key="i">
        <Picture :pictureData="pictureData[i]" :page="page" />
      </div>
    </div>
  </div>
  <div class="btn-bottom">
    <i class="fas fa-chevron-left" @click="goLeft"></i>
    <i class="far fa-circle" @click="goCenter"></i>
    <i class="fas fa-chevron-right" @click="goRight"></i>
  </div>
</template>

<script>
import Picture from "./Picture.vue";

export default {
  name: "Gallery",
  data() {
    return {
      currentPosition: -200,
    };
  },
  mounted() {
      let firstClone = this.$refs.container.firstElementChild.cloneNode(true);
      this.$refs.container.appendChild(firstClone);

      if (this.currentPosition == 100 || this.currentPosition == -600) {
        setInterval(this.goLeft, 10);
      } else {
        setInterval(this.goLeft, 3500);
      }
  },
  methods: {
    goLeft() {
      this.currentPosition += 100;
      
      this.$refs.container.style.transition = `3s`;
      this.$refs.container.style.transform = `translate(${this.currentPosition}vw)`;

      if (this.currentPosition == 100) {
        this.$refs.container.style.transition = `0s`;
        this.$refs.container.style.transform = `translate(-500vw)`;
        
        this.currentPosition = -500;
      }
    },
    goRight() {
      this.currentPosition -= 100;
      
      this.$refs.container.style.transition = `3s`;
      this.$refs.container.style.transform = `translate(${this.currentPosition}vw)`;

      if (this.currentPosition == -600) {
        this.$refs.container.style.transition = `0s`;
        this.$refs.container.style.transform = `translate(0vw)`;
        
        this.currentPosition = 0;
      }
    },
    goCenter() {
      this.$refs.container.style.transform = "translate(-200vw)";
    },
  },
  components: {
    Picture,
  },
  props: {
    pictureData: Array,
    page: Number,
  },
};
</script>

<style>
.gallery-container {
  width: 600vw;
  display: flex;
  flex-wrap: nowrap;
  position: relative;
  transform: translateX(-200vw);
}
.gallery-item {
  width: 100vw;
}
.btn-bottom {
  width: 100vw;
  margin: 0 auto;
  position: fixed;
  z-index: 100;
  bottom: 50px;
  display: flex;
  justify-content: center;
  color: white;
  text-shadow: darkslategrey 1px 1px 6px;
}
.btn-bottom i {
  font-size: 30px;
  margin: 20px;
}
@media screen and (max-width: 768px) {
  .btn-bottom {
    font-size: 10px;
    top: 400px;
  }
}
</style>